import React from 'react';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import connectImg from './img/connect.png';
import vrResultImg from './img/vr_result.png';
import vrSettingsImg from './img/vr_settings.png';
import indoorEnvImg from './img/indoor_environment.png';
import ledControlImg from './img/led_control.png';
import Divider from '@material-ui/core/Divider';
import { versionName } from './config';

const useStyles = makeStyles((theme) => ({
  header1: {
    fontSize : '150%',
    fontWeight: 'bold',
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  header2: {
    fontSize : '150%',
    fontWeight: 'bold',
  },
  img : {
    width : '60%',
    height: '60%',
    marginBottom: theme.spacing(5),
  },
  body: {
    fontSize : '150%',
    margin: theme.spacing(1.5),
  },

}));

function PageIsInstructions (props) {
    const classes = useStyles();

    return (
      <div>

        <Typography className = {classes.header1} >
          The version is {versionName}
        </Typography>

        <Divider />

        <Typography className = {classes.header1} >
          1. Click or tap "CONNECT" on the top right
        </Typography>
        <img className = {classes.img} src={connectImg} alt="connectImage" />

        <Typography className = {classes.header1} >
          2. The data received by BLE will be displayed on each page 
        </Typography>

        <Typography className = {classes.header2} >
          2.1. V/R Result 
        </Typography>
        <Typography className = {classes.body} >
          You can check voice recognition results and score.
        </Typography>
        <img className = {classes.img} src={vrResultImg} alt="voiceRecogResultImage" />

        <Typography className = {classes.header2} >
          2.2. V/R Settings 
        </Typography>
        <Typography className = {classes.body} >
          You can set parameters used in libraries. Confidence Threshold is available to  be set for each word. 
        </Typography>
        <img className = {classes.img} src={vrSettingsImg} alt="voiceRecogSettingsImage" />

        <Typography className = {classes.header2} >
          2.3. Indoor Environment  
        </Typography>
        <Typography className = {classes.body} >
          You can check current sensor data (temperature, humidity, CO2, TVOC, and IAQ). 
        </Typography>
        <img className = {classes.img} src={indoorEnvImg} alt="indoorEnvironmentImage" />

        <Typography className = {classes.header2} >
          2.4. LED Control  
        </Typography>
        <Typography className = {classes.body} >
          You can control LED1 on EK-RA4W1. 
        </Typography>
        <img className = {classes.img} src={ledControlImg} alt="ledControlImage" />

      </div>
    );

}




PageIsInstructions.propTypes = {
    window: PropTypes.func,
  };

export { PageIsInstructions };