import React from 'react';
import PropTypes from 'prop-types';
import { Select } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Box from '@material-ui/core/Box';
import Snackbar from '@material-ui/core/Snackbar';
import { makeStyles } from '@material-ui/core/styles';
import { bleDevice } from './ble'

const useStyles = makeStyles((theme) => ({
  box: {
    marginTop: theme.spacing(1.0),
    marginBottom: theme.spacing(0.5),
  },
  formControl: {
    marginTop: theme.spacing(1),
    minWidth: 120,
  },
  title: {
    fontSize: '150%',
    fontWeight: 'bold',
  },
  button: {
    marginTop: theme.spacing(2),    
    fontSize : '150%',
  }
}));

const isCfThreshold       = 1;
const isVdThreshold       = 2;
const isGain              = 3;
const isNoiseSuppressor   = 4;
const isBeamForming       = 5;

function PageIsVoiceRecogSettings (props) {
  const classes = useStyles();
  const [buttonClicked, setButtonClicked] = React.useState(false);
  const [snackbarState, setSnackbarState] = React.useState({
    open : false,
    vertical : 'bottom',
    horizontal: 'center',
    message : '',
    autoHideDuration : 1000,
  });

  const handleSnackbarOpen = (text) => {
    setSnackbarState({...snackbarState, open : true, message : text});
  }

  const handleSnackbarClose = () => {
    setSnackbarState({...snackbarState, open: false});
  }

  const writeVoiceRecognitionInformation = (wordId) => {
    let currentParams;
    let previousParams;
    let sendFlag = 0;

    if (props.masterWordInfo.length <= 1) {
      console.log('No wordInfo');
    }
    else if (wordId < props.masterWordInfo.length) {
      currentParams   = props.masterWordInfo[wordId].parameters;
      previousParams  = props.backupWordInfo[wordId].parameters;
      for (let paramId = isCfThreshold; paramId < currentParams.length; paramId++) {
        if (currentParams[paramId] !== previousParams[paramId]) {
          sendFlag = 1;
          break;
        }
        else {
          sendFlag = 0;
        }
      }

      if (sendFlag === 1) {
        return bleDevice.writeValueWithResponse (bleDevice.bleInfo.device,
                                                 bleDevice.bleInfo.characteristicInfo.voiceRecogParams.characteristic,
                                                 [
                                                   0,
                                                   wordId,
                                                   props.masterWordInfo[wordId].parameters[isCfThreshold],
                                                   (props.masterWordInfo[1].parameters[isVdThreshold] >> 8),
                                                   (props.masterWordInfo[1].parameters[isVdThreshold] & 0xFF),
                                                   props.masterWordInfo[1].parameters[isGain],
                                                   props.masterWordInfo[1].parameters[isNoiseSuppressor],
                                                   props.masterWordInfo[1].parameters[isBeamForming],
                                                 ])
        .then (_ => {
          return writeVoiceRecognitionInformation (wordId + 1);
        })
      }
      else {
        return writeVoiceRecognitionInformation (wordId + 1);
      }
    }
    else {
      return Promise.resolve();
    }
  }

  const handleSendButtonClicked = () => {
    let currentParam;
    let previousParam;

    if (props.masterWordInfo.length <= 1) {
      console.log('No wordInfo');
    }
    else {
      setButtonClicked(true);

      /* Send data */
      return Promise.resolve()
      .then(_=> {
        return writeVoiceRecognitionInformation(1);
      })
      .then(_ => {
        if (bleDevice.bleInfo.device.gatt.connected) {
          handleSnackbarOpen('Success');

          /* Set Confindence Threshold */
          for (let wordId = 1; wordId < props.masterWordInfo.length; wordId++) {
            currentParam  = props.masterWordInfo[wordId].parameters[isCfThreshold];
            previousParam = props.backupWordInfo[wordId].parameters[isCfThreshold];
            if (currentParam !== previousParam) {
              props.backupWordInfo[wordId].parameters[isCfThreshold] = currentParam;
            }
          }

          /* Set other parameters */
          for (let paramId = isVdThreshold; paramId < props.masterWordInfo[1].parameters.length; paramId++) {
            currentParam  = props.masterWordInfo[1].parameters[paramId];
            previousParam = props.backupWordInfo[1].parameters[paramId];
            if (currentParam !== previousParam) {
              props.backupWordInfo[1].parameters[paramId] = currentParam;
            }
          } 
        }
        else {
          // nothing
        }
        setButtonClicked(false);
        console.log('success');
      })
      .catch(error => {
        handleSnackbarOpen('Failed');
        setButtonClicked(false);
        console.log(error);
      });
    }
  }

  return (
    <div>
      <SelectConfideceThreshold masterWordInfo = {props.masterWordInfo} backupWordInfo = {props.backupWordInfo} />

      <SelectVoiceDetectionThreshold masterWordInfo = {props.masterWordInfo} backupWordInfo = {props.backupWordInfo} />

      <SelectGain masterWordInfo = {props.masterWordInfo} backupWordInfo = {props.backupWordInfo} />

      <SelectNoiseSuppressor masterWordInfo = {props.masterWordInfo} backupWordInfo = {props.backupWordInfo} />

      <SelectBeamForming masterWordInfo = {props.masterWordInfo} backupWordInfo = {props.backupWordInfo} />

      <Button 
      className = {classes.button} 
      variant = 'outlined' 
      color = 'primary' 
      fontWeight = 'bold'
      disabled = {Boolean(buttonClicked)}
      onClick = {handleSendButtonClicked}
      >
        APPLY
      </Button>

      <Snackbar
        open={snackbarState.open}
        onClose={handleSnackbarClose}
        message={snackbarState.message}
        key={snackbarState.vertical + snackbarState.horizontal}
        autoHideDuration={snackbarState.autoHideDuration}
      />

    </div>
  );
}

function InputParameterValue (props) {
  const classes = useStyles();
  const [paramvalue, setParamValue] = React.useState('');

  const handleParamValueChanged = (event) => {
    let value = event.target.value;

    /* Check range */
    if (value > props.maxValue) {
      value = props.maxValue;
    }
    else if (value < props.minValue) {
      value = props.minValue;
    }
    else {
      // nothing
    }

    /* Set value */
    if (props.masterWordInfo.length <= 1) {
      console.log('No wordInfo');
    }
    else if (props.parameterId === isCfThreshold) {
      if (props.wordId === '0') {
        for (let id = 1; id < props.masterWordInfo.length; id++) {
          props.masterWordInfo[id].parameters[isCfThreshold] = value - 0; // string -> number
        }
      }
      else {
        props.masterWordInfo[props.wordId].parameters[isCfThreshold] = value - 0; // string -> number
      }
    }
    else {
      props.masterWordInfo[props.wordId].parameters[props.parameterId] = value - 0; // string -> number
    }
    
    setParamValue(value);
  }

  React.useEffect(() => {
    if (props.backupWordInfo.length <= 1) {
      console.log('No wordInfo');
    }
    else if (props.wordId === '0') {
      setParamValue('');
    }
    else {
      setParamValue(props.backupWordInfo[props.wordId].parameters[props.parameterId] + ''); // number -> string
    }
    
  }, [props.backupWordInfo, props.wordId]);

  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <TextField
          id = 'param-value-text-field'
          label='Value'
          type='number'
          value = {paramvalue}
          onChange = {handleParamValueChanged}
        />
      </Box>
    </div>
  );
}

function SelectConfideceThreshold (props) {
  const classes = useStyles();
  const [wordId, setWordId] = React.useState('1');

  const handleWordIdChanged = (event) => {
    const value = event.target.value;

    if (value === '-1') {
      // nothing
    }
    else {
      setWordId(value);
    }    
  }

  React.useEffect(() => {
    if (props.masterWordInfo.length <= 1) {
      console.log('No wordInfo');
    }
    else{
      setWordId(wordId);
    }
    
  }, [props.masterWordInfo]);

  return (
    <div>
      <Box className = {classes.box}>
        <Typography className = {classes.title}>
          Confidence Threshold
        </Typography>

        <Box display = 'flex' flexDirection = 'row'>
          <Box display = 'flex' flexDirection = 'column'>
          {
            ((props.masterWordInfo.length <= 1)
            ?
              (
                <FormControl className={classes.formControl}>
                <InputLabel >Word ID</InputLabel>
                <Select
                  native
                  value={wordId}
                  onChange={handleWordIdChanged}
                >
                  <option aria-label='None' value={'-1'} />
                </Select>
                </FormControl>
              )
            :
              (
                <FormControl className={classes.formControl}>
                <InputLabel >Word ID</InputLabel>
                <Select
                  native
                  value={wordId}
                  onChange={handleWordIdChanged}
                >
                  {
                    props.masterWordInfo.map((info, index) => (
                      ((index === 0) ? 
                        (
                          <option value={'0'} key={index}>All</option>
                        )
                      :
                        (
                        <option value={info.wordId} key={index}>
                          No. {info.wordId}
                        </option>
                        )
                      )
                    ))
                  }
                </Select>
                </FormControl>
              )
            )
          }
          </Box>

          <Box display = 'flex' flexDirection = 'column' ml = {1.0}> 
            <InputParameterValue
            masterWordInfo = {props.masterWordInfo}
            backupWordInfo = {props.backupWordInfo}
            wordId = {wordId}
            paramsValue = {props.paramsValue}
            parameterId = {isCfThreshold}
            maxValue = {200}
            minValue = {0}
            />
          </Box>
        </Box>
      </Box>
    </div>

  );
}

function SelectVoiceDetectionThreshold (props) {
  const classes = useStyles();
  return (
    <div>
      <Box className = {classes.box} mt = {1}>
        <Typography className = {classes.title}>
          Voice Detection Threshold
        </Typography>

        <InputParameterValue
        masterWordInfo = {props.masterWordInfo}
        backupWordInfo = {props.backupWordInfo}
        wordId = {1}
        paramsValue = {props.paramsValue}
        parameterId = {isVdThreshold}
        maxValue = {15000}
        minValue = {0}
        />
      </Box>
    </div>
  );
}

function SelectGain (props) {
  const classes = useStyles();  
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Gain
        </Typography>

        <InputParameterValue
        masterWordInfo = {props.masterWordInfo}
        backupWordInfo = {props.backupWordInfo}
        wordId = {1}
        paramsValue = {props.paramsValue}
        parameterId = {isGain}
        maxValue = {9}
        minValue = {0}
        />
      </Box>
    </div>
  );
}

function SelectNoiseSuppressor (props) {
  const classes = useStyles();
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Noise Suppressor
        </Typography>

        <InputParameterValue
        masterWordInfo = {props.masterWordInfo}
        backupWordInfo = {props.backupWordInfo}
        wordId = {1}
        paramsValue = {props.paramsValue}
        parameterId = {isNoiseSuppressor}
        maxValue = {7}
        minValue = {0}
        />
      </Box>
    </div>
  );
}

function SelectBeamForming (props) {
  const classes = useStyles();  
  return (
    <div>
      <Box className = {classes.box} mt = {1} >
        <Typography className = {classes.title}>
          Beam Forming
        </Typography>

        <InputParameterValue
        masterWordInfo = {props.masterWordInfo}
        backupWordInfo = {props.backupWordInfo}
        wordId = {1}
        paramsValue = {props.paramsValue}
        parameterId = {isBeamForming}
        maxValue = {7}
        minValue = {0}
        />
      </Box>
    </div>
  );
}


PageIsVoiceRecogSettings.propTypes = {
    window: PropTypes.func,
  };

export { PageIsVoiceRecogSettings };