import React from 'react';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';
import ledBlack from './img/led_black.png';
import ledRed from './img/led_red.png';
import ledGreen from './img/led_green.png';
import ledBlue from './img/led_blue.png';
import funStop from './img/fun_stop.png';
import funNormalSpeed from './img/fun_normal_speed.gif';
import funLowSpeed from './img/fun_low_speed.gif';
import funHighSpeed from './img/fun_high_speed.gif';
import funSwing from './img/fun_swing.gif';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';

// const isNotConnected  = 0;
// const isBeingDetected = 1;
const isUndetected    = 2;
const isFound         = 3;
const isNotFound      = 4;
const isUnconfidence  = 5;
const renesasBlue     = '#2a289d';
const renesasGray     = '#333333';

const voiceRecogStatusText = [
  'Click or tap "CONNECT" on the top right',
  'Detecting...',
  'Please say something!',
  'Success!',
  "No match",
  'Failed...',
];

const ledImage = [
  ledBlack,
  ledRed,
  ledGreen,
  ledBlue,  
];

const electricFunImage = [
  funStop,
  funNormalSpeed,
  funSwing,
  funHighSpeed,
  funLowSpeed,
];

const ledWordLength = 3;
const electricFunWordLength = 4;

const maxLogLength = 100;
let logText = [];

const useStyles = makeStyles((theme) => ({
  ledImg : {
    width : '25%',
    height: '25%',
    marginTop : theme.spacing(5),
    marginBottom : theme.spacing(5),
    marginRight : theme.spacing(10)
  },
  electricFunImg : {
    width : '25%',
    height: '25%',
    marginTop : theme.spacing(5),
    marginBottom : theme.spacing(5),
  },
  statusText : {
    fontSize: '150%', 
    fontWeight: 'bold',
    color: renesasGray,
  },
  logList : {
    width: '100%',
    maxWidth: '100%',
    position: 'relative',
    overflow: 'auto',
    maxHeight: 200,
    backgroundColor: theme.palette.background.default,
  },
  logTitle : {
    fontSize: '150%', 
    fontWeight: 'bold', 
    color: renesasGray,
    padding: 0,
    backgroundColor: 'inherit',
  },
  logText : {
    color: renesasGray,
    padding: 0,
    backgroundColor: 'inherit',
  },
}));

const getCurrentDateText = () => {
  const dateFullSize = new Date();
  const year    = dateFullSize.getFullYear();
  const month   = dateFullSize.getMonth() + 1;
  const date    = dateFullSize.getDate();
  const hours   = dateFullSize.getHours();
  const minutes = dateFullSize.getMinutes();
  const seconds = dateFullSize.getSeconds();
  const text = '[' + year + '/' + month + '/' + date + ' ' + hours + ':' + minutes + ':' + seconds + ']';

  return text;
} 

function PageIsVoiceRecogResult (props) { 
  return (
    <div>

      <DrawVoiceRecogStatus 
        status = {props.voiceRecogResult.status} 
        wordId = {props.voiceRecogResult.wordId}
        score = {props.voiceRecogResult.score}
      />

      <Divider/>

      <Box>
       <DrawImage
        status = {props.voiceRecogResult.status} 
        wordId = {props.voiceRecogResult.wordId}
        />
      </Box>

      <Divider/>

      <DrawLogText 
        status = {props.voiceRecogResult.status} 
        wordId = {props.voiceRecogResult.wordId}
        score = {props.voiceRecogResult.score}
      />

    </div>
  );
}

function DrawImage (props) {
  const classes = useStyles();
  const [led, setLed] = React.useState(ledImage[0]);
  const [electricFun, setElectricFun] = React.useState(electricFunImage[0]);

  React.useEffect(() => {

    switch (props.status) {
      case isFound :
        if (props.wordId <= ledWordLength) {
          setLed(ledImage[props.wordId]);
        }
        else if (props.wordId <= (ledWordLength + electricFunWordLength)) {
          setElectricFun(electricFunImage[props.wordId - ledWordLength]);
        }
        else {
          // nothing
        }
      break;

      default :
      break;
    }

  }, [props.status]);

  return (
    <div>
      <img className = {classes.ledImg} src={led} alt="ledImage" />

      <img className = {classes.electricFunImg} src={electricFun} alt="electricFunImage" />
    </div>

  );

}

function DrawVoiceRecogStatus(props) {
  const classes = useStyles();
  const [status, setStatus] = React.useState(props.status);
  const timeOutId = React.useRef(null);
  const statusText = React.useRef(voiceRecogStatusText[props.status]);
  const resultsText = React.useRef('No results');
  const scoreText = React.useRef('No score');
  const resultTextStyle = React.useRef({
    fontSize: '200%', 
    fontWeight: 'bold',
    color: renesasGray,
  });

  React.useEffect(() => {

    switch (props.status) {
      case isFound :
        resultsText.current ='OK. Word ID is ' + props.wordId + '.';
        scoreText.current = 'Score is ' + props.score + '.';
        resultTextStyle.current = {...resultTextStyle.current, color : renesasBlue};
      break;

      case isNotFound :
        resultsText.current = 'No results';
        scoreText.current = 'No score';
        resultTextStyle.current = {...resultTextStyle.current, color : renesasGray};
      break;

      case isUnconfidence :
        resultsText.current ='NG. Word ID is ' + props.wordId + '.';
        scoreText.current = 'Score is ' + props.score + '.';
        resultTextStyle.current = {...resultTextStyle.current, color : 'red'};
      break;

      default :
      break;
    }

    statusText.current = voiceRecogStatusText[props.status];
    
    if (props.status === isUndetected) {
      switch (status) {
        case isFound : 
          timeOutId.current = setTimeout(() => {
            setStatus(props.status);
          }, 3000);
        break;

        case isNotFound :
          timeOutId.current = setTimeout(() => {
            setStatus(props.status);
          }, 3000);
        break;

        case isUnconfidence :
          timeOutId.current = setTimeout(() => {
            setStatus(props.status);
          }, 3000);
        break;

        default :
          setStatus(props.status);
        break;
      }
    }
    else {
      setStatus(props.status);
    }    

    return () => {
      if (timeOutId.current) clearTimeout(timeOutId.current);
      timeOutId.current = null;
    }   

  }, [props.status]);

  return (
    <Box mt={1} mb={2}>
      <Typography className = {classes.statusText}>
        {statusText.current}
      </Typography>
      <Typography style = {resultTextStyle.current}>
        {resultsText.current} 
      </Typography>
      <Typography style = {resultTextStyle.current}>
        {scoreText.current} 
      </Typography>
    </Box>
  );
}

function DrawLogText(props) {
  const classes = useStyles();
  const [logLength, setLogLength] = React.useState(logText.length);

  React.useEffect(() => {
    let oldLog;
    let currentLog;
    switch (props.status) {
      case isFound :
        currentLog = getCurrentDateText() + ' OK ' + props.wordId + ' ' + props.score;
        if (logLength >= maxLogLength) {
          oldLog = logText.slice(0, -1);
          logText = [currentLog, ...oldLog];
          setLogLength(maxLogLength);
        }
        else {
          logText = [currentLog, ...logText];
          setLogLength(logLength + 1);
        }
        
      break;

      case isUnconfidence :
        currentLog = getCurrentDateText() + ' NG ' + props.wordId + ' ' + props.score;
        if (logLength >= maxLogLength) {
          oldLog = logText.slice(1);
          logText = [currentLog, ...oldLog];
          setLogLength(maxLogLength);
        }
        else {
          logText = [currentLog, ...logText];
          setLogLength(logLength + 1);
        }
      break;

      default :
      break;
    }

  }, [props.status]);

  return (
    <Box >
      <List className = {classes.logList} subheader={<li />}>
        <ListSubheader className = {classes.logTitle}>
          {'Result Log'}
        </ListSubheader>
        {
          logText.map((text, index) => (
            <ListItem key = {index} className = {classes.logText}>
              <ListItemText primary = {text} />
            </ListItem>
          ))
        }
      </List>
    </Box>
  );
}

PageIsVoiceRecogResult.propTypes = {
    window: PropTypes.func,
  };

export { PageIsVoiceRecogResult };