
class BleDevice {
    constructor () {  
        this.bleInfo = {
            deviceName : 'RBLE-DEV',
            device : null,
            server : null,
            onDisconnectedHandler : null,
            serviceInfo : {
                uuid : '06ec8162-e3cd-45af-942a-e079b4e65cb7',
                service : null,
            },
            characteristicInfo  : {
                voiceRecogResult : {
                    uuid : '996abc1b-d72c-4cad-af9e-484c8f4ad0cd',
                    characteristic : null,
                    handler : null,
                },
                voiceRecogParams : {
                    uuid : 'a1dbeea2-f7fb-4abd-9bda-02714853aac5',
                    characteristic : null,
                },
                ledControl : {
                    uuid : '6c1100f6-7773-41d8-9ec9-766af1c629f6',
                    characteristic : null,
                },
                envSensor : {
                    uuid : 'cc976fae-0440-4e22-b54b-12312785810a',
                    characteristic : null,
                    handler : null,
                },
            },
        };
        
        this.options = {
            filters: [
                {namePrefix: "RBLE"}
            ],
            optionalServices: [this.bleInfo.serviceInfo.uuid]
        };  
    }

    connectDevice(device, options, handler) {
        if (!options) {
            console.log("No options");
            return Promise.resolve();
        }
        else if (!device) {
            return this.requestDevice(options)
            .then(device => {
                console.log(device);
                return this.connectServer(device, handler);
            });
        }
        else {
            return this.connectServer(device, handler);
        }
    }

    requestDevice(options) {
        if (!options) {
            console.log("No options");
            return Promise.resolve();
        }
        else {
            console.log("Request the device");
            return navigator.bluetooth.requestDevice(options);
        }
    }

    connectServer(device, handler) {
        if ((!device) || !handler) {
            console.log("No device or handler");
            return Promise.resolve();
        }
        else {
            this.bleInfo.device = device;
            this.bleInfo.onDisconnectedHandler = handler;
            this.bleInfo.device.addEventListener('gattserverdisconnected', handler);
            console.log("Connect the GATT server");
            return device.gatt.connect();
        }
    }

    getService(server, uuid) {
        if ((!server) || (!uuid)) {
            console.log("No server");
            return Promise.resolve();
        }
        else {
            this.bleInfo.server = server;
            console.log("Get the service");
            return server.getPrimaryService(uuid);
        }
    }

    getCharacteristics(service) {
        if (!service) {
            console.log("No service");
            return Promise.resolve();
        }
        else {
            this.bleInfo.serviceInfo.service = service;
            console.log("Get characteristics");
            return service.getCharacteristics();
        }
    }

    getCharacteristic(service, uuid) {
        if ((!service) || (!uuid)) {
            console.log("No service or uuid");
            return Promise.resolve();
        }
        else {
            this.bleInfo.serviceInfo.service = service;
            console.log("Get characteristic");
            return service.getCharacteristic(uuid);
        }
    }

    setCharacteristic(characteristics) {
        if (!characteristics) {
            console.log("No characteristics");
            return Promise.resolve();
        }
        else {
            characteristics.forEach(characteristic => {
                console.log(characteristic);
                switch (characteristic.uuid) {
                    case this.bleInfo.characteristicInfo.voiceRecogResult.uuid :
                        this.bleInfo.characteristicInfo.voiceRecogResult.characteristic = characteristic;
                    break;

                    case this.bleInfo.characteristicInfo.voiceRecogParams.uuid :
                        this.bleInfo.characteristicInfo.voiceRecogParams.characteristic = characteristic;
                    break;

                    case this.bleInfo.characteristicInfo.ledControl.uuid :
                        this.bleInfo.characteristicInfo.ledControl.characteristic = characteristic;
                    break;

                    case this.bleInfo.characteristicInfo.envSensor.uuid :
                        this.bleInfo.characteristicInfo.envSensor.characteristic = characteristic;
                    break;

                    default :
                    break;
                }
            })
        }

        return Promise.resolve();
    }

    readValue(device, characteristic) {
        if (!characteristic) {
            console.log("No characteristic");
            return Promise.resolve();
        }
        else if (!device.gatt.connected) {
            console.log("No connection");
            return Promise.resolve();
        }
        else {
            console.log("Read value");
            return characteristic.readValue();
        }
    }

    writeValueWithResponse (device, characteristic, value) {
        if ((!characteristic) || (!value)) {
            console.log("No characteristic or value");
            return Promise.resolve();
        }
        else if (!device.gatt.connected) {
            console.log("No connection");
            return Promise.resolve();
        }
        else {
            // value is an array (e.g. [1,2,3])
            let writeValue = new Uint8Array(value);       
            console.log("Write value with response");
            return characteristic.writeValueWithResponse(writeValue);
        }
    }

    writeValueWithoutResponse (device, characteristic, value) {
        if ((!characteristic) || (!value)) {
            console.log("No characteristic or value");
            return Promise.resolve();
        }
        else if (!device.gatt.connected) {
            console.log("No connection");
            return Promise.resolve();
        }
        else {
            // value is an array (e.g. [1,2,3])
            let writeValue = new Uint8Array(value);       
            console.log("Write value without response");
            return characteristic.writeValueWithoutResponse(writeValue);
        }
    }

    startNotifications(characteristicName, handler) {
        if ((!characteristicName) || (!handler)) {
            console.log("No characteristic or handler");
            return Promise.resolve();
        }
        else {
            console.log('start notifications');
            characteristicName.handler = handler;
            characteristicName.characteristic.addEventListener('characteristicvaluechanged', handler);
            return characteristicName.characteristic.startNotifications();
        }
    }

    disconnect(device) {   
        if (!device) {
            console.log("No device");
            return;
        }      
        else if (device.gatt.connected) {
            console.log("Disconnect device");
            return device.gatt.disconnect();
        } 
        else {
            console.log("Disconeected : No connection");
            return;
        }   
    }
}

const bleDevice = new BleDevice();

 export { bleDevice };